@use "sass:color";
@use "../../const";
@use "../../palette";

:root {
    --sleek-dropdown-item-hover-background-color: #{palette.$slate-100};
}

.dropdown {
    position: relative;

    &__toggle {
        &:not(.-disabled) {
            cursor: pointer;
        }

        &.form__field {
            &::before {
                content: "";
                position: absolute;
                width: 2.5rem;
                top: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
                background-color: var(--sleek-surface-background-color);
                background: linear-gradient(
                    to right,
                    transparent,
                    var(--sleek-surface-background-color) 1rem,
                    var(--sleek-surface-background-color) 100%
                );
                pointer-events: none;
            }

            &::after {
                content: "";
                position: absolute;
                width: 0;
                height: 0;
                top: 50%;
                right: 0;
                z-index: 2;
                margin: -0.125rem 0.5rem 0 0;
                border-left: 0.25rem solid transparent;
                border-top: 0.25rem solid
                    color.scale(palette.$dark, $lightness: 25%);
                border-right: 0.25rem solid transparent;
            }
        }
    }

    &__toggle + &__search input {
        width: 100%;
    }

    &__menu {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 1000;
        min-width: 150px;
        min-width: max-content;
        margin: 0.25rem 0;
        padding: 0.25rem;
        border: 1px solid var(--sleek-surface-border-color);
        border-radius: 0.25rem;
        text-align: left;
        background-color: var(--sleek-surface-background-color);
        box-shadow: 0 0.25rem 0.5rem var(--sleek-shadow-color-3);

        &.-right {
            right: 0;
            left: auto;
        }

        &.-scroll {
            overflow-y: auto;
            -webkit-overflow-scrolling: touch;
        }

        .dropdown__search {
            padding: 0.5rem;
        }

        // Nested menu (sub-menus)
        .dropdown__menu {
            top: 0;
            left: 100%;
        }
    }

    &__item {
        padding: 0.5rem 1rem;
        border-radius: 0.25rem;
        color: var(--sleek-text-default-color);
        transition: background-color const.$fast ease-in;

        &.-link {
            text-decoration: none;

            &:hover,
            &:focus {
                text-decoration: none;
                color: var(--sleek-text-default-color);
            }
        }

        &.-drop {
            position: relative;
            padding-right: 3rem;

            &::after {
                content: "";
                position: absolute;
                width: 0;
                height: 0;
                top: 50%;
                right: 0;
                z-index: 2;
                margin: -0.3125rem 0.75rem 0 0;
                border-top: 0.3125rem solid transparent;
                border-left: 0.3125rem solid
                    color.scale(palette.$dark, $lightness: 25%);
                border-bottom: 0.3125rem solid transparent;
            }
        }

        &.-active {
            position: relative;
            background-color: var(--sleek-dropdown-item-hover-background-color);
            box-shadow: inset 0.5rem 0 0 -0.25rem palette.$blue;
        }

        &.-disabled {
            opacity: 0.75;
            cursor: not-allowed;
        }

        .icon + .text {
            margin-left: 0.25rem;
        }

        .badge {
            float: right;
            margin-left: 2rem;
        }
    }

    &__head {
        padding: 0.5rem 1rem 0.25rem;
        color: color.scale(palette.$dark, $lightness: 50%);
        font-size: 0.95em;
    }

    &__tool {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 1000;
        margin: 0.25rem 0;
        padding: 0.25rem 0.5rem;
        border: 1px solid var(--sleek-surface-border-color);
        border-radius: 0.25rem;
        background-color: var(--sleek-surface-background-color);
        box-shadow: 0 0.0625rem 0.125rem var(--sleek-shadow-color-1);
    }

    &__divider {
        margin: 0.25rem -0.25rem;
        border-bottom: 1px solid var(--sleek-divider-border-color);

        &:first-child,
        &:last-child {
            display: none;
        }
    }

    &__icon {
        &.-left {
            margin-right: 0.5rem;
        }

        &.-right {
            float: right;
            margin-left: 1rem;
        }
    }

    &.-open {
        > .dropdown__menu {
            display: flex;
        }

        &:not(.-readonly) {
            > .dropdown__tool {
                display: block;
            }
        }
    }

    // Styles for toggle inside an open parent
    .-open > .dropdown__toggle {
        background-color: var(--sleek-dropdown-item-hover-background-color);
    }

    &.-notifications {
        .dropdown__menu {
            width: 300px;
        }
    }

    &.-select {
        .dropdown__toggle {
            position: relative;
            padding-right: 1.5rem;
            overflow: hidden;
            white-space: nowrap;

            input[type="radio"] {
                display: none;

                + * {
                    display: none;
                }

                &:checked + * {
                    display: block;
                }
            }
        }

        .dropdown__menu {
            width: 100%;

            label {
                cursor: pointer;
            }
        }

        select {
            display: none;
        }
    }

    &:not(.-readonly) {
        .dropdown__item {
            &:not(.-disabled) {
                cursor: pointer;

                &:hover,
                &:focus {
                    background-color: var(
                        --sleek-dropdown-item-hover-background-color
                    );
                }
            }
        }
    }

    &.-readonly {
        .dropdown__item {
            cursor: default;
        }
    }
}
